@import url(https://fonts.googleapis.com/css?family=Poppins:200,300,400,500,600,700,800,900&display=swap);
:root {
  --coloured_section: #666;
  --coloured_section_font: #DDD;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Poppins";
}

section {
  padding-left: 4vw;
  height: 100vh;
}

#landing, #projects {
  background-color: #666;
  background-color: var(--coloured_section);
  color: #DDD;
  color: var(--coloured_section_font);
}

#landing {
  background-color: #000;
}

#landing h1 {
  font-size: 6rem;
  font-weight: 700;
}

#projects {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#footer {
  height: unset;
  text-align: center;
  color:#666;
}
.EnergyButton_eButton__2hJHC {
  position: relative;
  display: inline-block;
  padding: 15px 30px;
  border: 2px solid #0f0;
  text-transform: uppercase;
  font-weight: 600;
  text-decoration: none;
  letter-spacing: 2px;
  color: #fff;
  font-family: 'Poppins', sans-serif;
  -webkit-box-reflect: below 0px linear-gradient(transparent, #0002);
  transition: 0.5s;
  transition-delay: 0s;
}

.EnergyButton_eButton__2hJHC:hover {
  transition-delay: 1.5s;
  color: #000;
  box-shadow: 0 0 10px #0f0,
              0 0 20px #0f0,
              0 0 40px #0f0,
              0 0 80px #0f0,
              0 0 160px #0f0;
}
.EnergyButton_eButton__2hJHC span {
  position: relative;
  z-index: 100;
}

/* Left lines */
.EnergyButton_eButton__2hJHC::before {
  content: '';
  position: absolute;
  left: -20px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 20px;
  height: 2px;
  background-color: #0f0;
  box-shadow: 5px -8px 0 #0f0, 
              5px 8px 0 #0f0;
  transition: width 0.5s, left 0.5s, height 0.5s,
  box-shadow 0.5s;
  transition-delay: 1s,0.5s,0s,0s;
}

.EnergyButton_eButton__2hJHC:hover::before {
  width: 60%;
  height: 100%;
  left: -2px;
  box-shadow: 5px 0 0 #0f0, 
              5px 0 0 #0f0;
  transition-delay: 0s,0.5s,1s,1s;
}

/* Right lines */
.EnergyButton_eButton__2hJHC::after {
  content: '';
  position: absolute;
  right: -20px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 20px;
  height: 2px;
  background-color: #0f0;
  box-shadow: -5px -8px 0 #0f0, 
              -5px 8px 0 #0f0;
  transition: width 0.5s, left 0.5s, height 0.5s,
  box-shadow 0.5s;
  transition-delay: 1s,0.5s,0s,0s;
}

.EnergyButton_eButton__2hJHC:hover::after {
  width: 60%;
  height: 100%;
  right: -2px;
  box-shadow: -5px 0 0 #0f0, 
              -5px 0 0 #0f0;
  transition-delay: 0s,0.5s,1s,1s;
}
.Socials_container__In0aL {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;

  height: 70vh;
  position: fixed;
  top: 15vh;
  left: 0;
  padding: 0.5rem;
  border: 2px solid silver;
  background-color: #FFFFFF88;
  border-radius: 0 20px 20px 0;

  overflow: hidden;
}

.Socials_indicator__2nzES {
  background-color: #0f0;
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.ProjectCard_card__2E7p4 {
  color: #FFF;
  position: relative;
  width: 600px;
  height: 350px;
  border-radius: 20px;
  display: flex;
  align-items: center;
  transition: 0.5s;
}

.ProjectCard_circle__3cHuI {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 20px;
  overflow: hidden;
}

.ProjectCard_circle__3cHuI::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #ffe1a8;
  -webkit-clip-path: circle(120px at center);
          clip-path: circle(120px at center);
  transition: 0.5s;
}

.ProjectCard_card__2E7p4:hover .ProjectCard_circle__3cHuI::before {
  background: #472d30;
  -webkit-clip-path: circle(400px at center);
          clip-path: circle(400px at center);
}

.ProjectCard_card__2E7p4 img {
  position: absolute;
  top: 45%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  height: 350px;
  pointer-events: none;
  transition: 0.5s;
}

.ProjectCard_card__2E7p4:hover img {
  left: 75%;
  height: 500px;
}

.ProjectCard_content__2mSNi {
  position: relative;
  width: 50%;
  left: 20%;
  padding: 20px 20px 20px 40px;
  transition: 0.4s;
  opacity: 0;
  visibility: hidden;
}

.ProjectCard_card__2E7p4:hover .ProjectCard_content__2mSNi {
  left: 0;
  visibility: visible;
  opacity: 1;
}

.ProjectCard_content__2mSNi h2 {
  text-transform: uppercase;
  font-size: 2em;
  line-height: 1em;
}

.ProjectCard_content__2mSNi a {
  position: relative;
  padding: 10px 20px;
  border-radius: 10px;
  background: #FFF;
  color: #111;
  margin-top: 10px;
  display: inline-block;
  text-decoration: none;
  font-weight: 700;
}
